import {MyOrderList} from '../../common/api.js'
export default {
	data() {
		return {
			tabArr:[
				{
					tid:0,
					tab_name:"全部",
					isActive:false
				},
				{
					tid:1,
					tab_name:"待付款",
					isActive:false
				},
				{
					tid:2,
					tab_name:"待发货",
					isActive:false
				},{
					tid:3,
					tab_name:"待收货",
					isActive:false
				},{
					tid:4,
					tab_name:"退货",
					isActive:false
				}
			],
			pageData:{
				page:1,
				limit:100,
				order_type:0
			},
			orderList:[]
		}
	},
	onLoad(option){
		this.tabArr[option.tid].isActive = true;
		this.pageData.order_type = option.tid;
		this.pageInit();
	},
	methods: {
		pageInit(){
			MyOrderList(this.pageData).then(res=>{
				if(res.status){
					this.orderList = [...res.data.data];
				}
			})
		},
		tabChange(item){
			this.tabArr.forEach((e)=>{
				e.isActive = false;
			})
			item.isActive = true;
			this.pageData.order_type = item.tid;
			this.pageInit();
		},
		toDetail(id){
			uni.navigateTo({
			    url: '../orderDetail/orderDetail?oid='+id
			});
		}
	}
}
